
import { onMounted, ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";

export default {
  name: "IncotermsSelect",
  props: {
    incoterms_id: {
      type: String,
      default: "",
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:incoterms_id"],

  setup(props) {
    const element_id = ref(undefined);
    const inputIncoterm = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });

    const getIncoterms = () => {
      ApiService.get("/api/lima/icoterms").then(({ data }) => {
        console.log("data", data);
        inputIncoterm.value.list = data;
        inputIncoterm.value.options = data;
      });
    };

    const selectElement = (query) => {
      if (query !== "") {
        inputIncoterm.value.loading = true;
        setTimeout(() => {
          inputIncoterm.value.loading = false;
          inputIncoterm.value.options = inputIncoterm.value.list.filter(
            (item: Record<string, any>) => {
              return (
                item.vktext.toLowerCase().indexOf(query.toLowerCase()) > -1
              );
            }
          );
        }, 200);
      } else {
        inputIncoterm.value.options = [];
      }
    };

    watch(
      () => props.incoterms_id,
      (first) => {
        element_id.value = first;
      }
    );

    onMounted(() => getIncoterms());

    return {
      element_id,
      inputIncoterm,
      selectElement,
    };
  },
};
