<template>
  <div class="booking-form">
    <Form class="form" @submit="makeBooking">
      <div class="row container-form">
        <div v-if="!isauth" class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("fullname") }}</label>
          <Field
            v-model="form.name"
            type="text"
            name="fullname"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div v-if="!isauth" class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("email") }}</label>
          <Field
            v-model="form.email"
            type="text"
            name="email"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div v-if="!isauth" class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("phonenumber") }}</label>
          <Field
            v-model="form.phone"
            type="text"
            name="phone"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("pol") }}</label>
          <Field
            v-model="form.port_loading"
            readonly
            type="text"
            name="pol"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("pod") }}</label>
          <Field
            v-model="form.port_discharge"
            readonly
            type="text"
            name="pod"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4">
          <label class="form-label">{{ $t("loadavailability") }}</label>
          <Datepicker
            v-model="form.ready_date"
            format="dd-MM-yyyy"
            :max-date="date_max"
          ></Datepicker>
          <!--          <input
            v-model="form.ready_date"
            type="Date"
            class="date-picker"
            :max="date_max"
            placeholder="dd-mm-yyyy"
          />-->
          <!--          <el-date-picker
            v-model="form.ready_date"
            class="date-picker"
            format="DD-MM-YYYY"
          />-->
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("vessel") }}</label>
          <Field
            v-model="form.vessel"
            readonly
            type="text"
            name="vessel"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("trip") }}</label>
          <Field
            v-model="form.voyage"
            readonly
            type="text"
            name="trip"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <!--        <div
          v-if="user.customer_number !== 'FAK' && Object.keys(user).length"
          class="col-md-6 col-lg-4 mt-3"
        >
          <label class="form-label">{{ $t("customerreference") }}</label>
          <Field
            v-model="form.customer_reference"
            readonly
            type="text"
            name="reference"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>-->
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">Incoterms</label>
          <IncotermsSelect
            :incoterms_id="form.incoterms"
            @update:incoterms_id="form.incoterms = $event"
          />
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("currency") }}</label>
          <Field
            v-model="form.currency"
            readonly
            type="text"
            name="currency"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("payment") }}</label>
          <Field
            v-model="form.payability"
            readonly
            type="text"
            name="payment"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("shippername") }}</label>
          <Field
            v-model="form.shipper_name"
            type="text"
            name="load_name"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("shippercontact") }}</label>
          <Field
            v-model="form.shipper_contact_name"
            type="text"
            name="load_contact_person"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("shipperphone") }}</label>
          <Field
            v-model="form.shipper_contact_telephone"
            type="text"
            name="shipper_contact_telephone"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("shipperemail") }}</label>
          <Field
            v-model="form.shipper_contact_email"
            type="text"
            name="shipper_contact_email"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("address") }}</label>
          <Field
            v-model="form.additional_inf_1"
            type="text"
            name="aditional_info1"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("postalcode") }}</label>
          <Field
            v-model="form.additional_inf_2"
            type="text"
            name="aditional_info2"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("citycountry") }}</label>
          <Field
            v-model="form.additional_inf_3"
            type="text"
            name="aditional_info3"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("numberpackages") }}</label>
          <Field
            v-model="form.number_packages"
            type="text"
            name="packages_number"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("containertype") }}</label>
          <Field
            v-model="form.container_syze_type"
            readonly
            type="text"
            name="article_type"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("customstariffcode") }}</label>
          <TariffCodeSelect
            :cccode_id="form.item_commodity"
            @updateCode="updateTariffCode"
          />
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("goodsdescription") }}</label>
          <Field
            v-model="form.item_description_goods"
            readonly
            type="text"
            name="description"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("grossweight") }}</label>
          <Field
            v-model="form.item_cargo_gross_weight"
            type="text"
            name="brute_weight"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("netweight") }}</label>
          <Field
            v-model="form.item_cargo_net_weight"
            type="text"
            name="net_weight"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-4 mt-3">
          <label class="form-label">{{ $t("quotationnumber") }}</label>
          <Field
            v-model="form.special_quotation"
            readonly
            type="text"
            name="quotation"
            autocomplete="off"
            class="form-control form-control-website"
          ></Field>
        </div>
        <div class="col-md-6 col-lg-12 mt-3">
          <label class="form-label">{{ $t("notes") }}</label>
          <textarea
            v-model="form.notes"
            class="form-control form-control-website"
            data-kt-autosize="true"
            type="text"
            name="notes"
            autocomplete="off"
          ></textarea>
        </div>
        <div class="col-12 d-flex justify-content-center">
          <div class="row mt-5">
            <div class="col-md-12">
              <div class="text-center">
                <button ref="submitButton" class="container-button">
                  <span v-if="!loading"> {{ $t("book") }} </span>
                  <span v-else>
                    {{ $t("wait") }}
                    <span
                      class="spinner-border spinner-border-sm align-middle ms-2"
                    ></span>
                  </span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Form>
  </div>
</template>

<script>
import { defineComponent, ref, computed, onMounted } from "vue";
import { Form, Field } from "vee-validate";
import ApiService from "@/core/services/ApiService";
import IncotermsSelect from "@/components/website/selects/IncotermsSelect.vue";
import TariffCodeSelect from "@/components/website/selects/TariffCodeSelect.vue";
import { useStore } from "vuex";
import { ElNotification } from "element-plus";
import { useI18n } from "vue-i18n";
import Datepicker from "@vuepic/vue-datepicker";
import "@vuepic/vue-datepicker/dist/main.css";

export default defineComponent({
  name: "Booking",
  components: {
    Form,
    Field,
    IncotermsSelect,
    TariffCodeSelect,
    Datepicker,
  },
  props: {
    voyage: {
      type: Object,
      default() {
        return {};
      },
    },
    direct: {
      type: Boolean,
      default: false,
    },
    quotations: {
      type: Array,
      default() {
        return [];
      },
    },
  },

  setup(props) {
    const { t } = useI18n();
    const store = useStore();
    const loading = ref(false);
    const user = computed(() => store.getters.currentUser);
    const isauth = computed(() => store.getters.isUserAuthenticated);
    const bookingQuotation = computed(() => store.getters.BookingQuotationt);
    const date_max = computed(() => store.getters.DiscountDate);
    const form = ref({
      name: "",
      email: "",
      phone: "",
      port_loading: "",
      port_discharge: "",
      ready_date: "",
      vessel: "",
      voyage: "",
      customer_reference: "",
      incoterms: "",
      currency: "",
      payability: "Prepago",
      adress_type: "SH",
      shipper_name: "",
      shipper_contact_name: "",
      shipper_contact_telephone: "",
      shipper_contact_email: "",
      additional_inf_1: "",
      additional_inf_2: "",
      additional_inf_3: "",
      number_packages: "",
      container_syze_type: "",
      item_commodity: "",
      item_description_goods: "",
      item_cargo_gross_weight: "",
      item_cargo_net_weight: "",
      special_quotation: "",
      service: "",
      notes: "",
    });
    const submitButton = (ref < HTMLElement) | (null > null);

    const updateTariffCode = (item) => {
      form.value.item_commodity = item.cccode;
      form.value.item_description_goods = item.ccbez;
    };

    const makeBooking = () => {
      if (
        (!isauth.value && form.value.name === "") ||
        (!isauth.value && form.value.email === "") ||
        (!isauth.value && form.value.phone === "")
      ) {
        ElNotification({
          title: "Error",
          message: t("vbookingform1"),
          type: "error",
        });
      } else if (
        form.value.ready_date === "" ||
        form.value.incoterms === "" ||
        form.value.number_packages === "" ||
        form.value.container_syze_type === "" ||
        form.value.item_commodity === "" ||
        form.value.item_description_goods === "" ||
        form.value.item_cargo_gross_weight === "" ||
        form.value.item_cargo_net_weight === ""
      ) {
        ElNotification({
          title: "Error",
          message: t("vbookingform2"),
          type: "error",
        });
      } else {
        loading.value = true;
        ApiService.post("/api/bookings", {
          quotations: props.quotations,
          name: form.value.name,
          email: form.value.email,
          phone: form.value.phone,
          port_loading: props.direct
            ? props.voyage.POL
            : props.voyage.header.tmh_pol,
          port_discharge: props.direct
            ? props.voyage.POD
            : props.voyage.header.tmh_pod,
          ready_date: form.value.ready_date,
          vessel: props.direct
            ? props.voyage.rhvessel
            : props.voyage.header.rhvessel,
          voyage: props.direct
            ? props.voyage.rhreise
            : props.voyage.header.rhreise,
          customer_reference: form.value.customer_reference,
          incoterms: form.value.incoterms,
          currency: form.value.currency,
          payability: "P",
          adress_type: form.value.adress_type,
          shipper_name: form.value.shipper_name,
          shipper_contact_name: form.value.shipper_contact_name,
          shipper_contact_telephone: form.value.shipper_contact_telephone,
          shipper_contact_email: form.value.shipper_contact_email,
          additional_inf_1: form.value.additional_inf_1,
          additional_inf_2: form.value.additional_inf_2,
          additional_inf_3: form.value.additional_inf_3,
          number_packages: form.value.number_packages,
          container_syze_type: form.value.container_syze_type,
          item_commodity: form.value.item_commodity,
          item_description_goods: form.value.item_description_goods,
          item_cargo_gross_weight: form.value.item_cargo_gross_weight,
          item_cargo_net_weight: form.value.item_cargo_net_weight,
          special_quotation: form.value.special_quotation,
          service: form.value.service,
          is_direct: props.direct ? 1 : 0,
          port_discharge_description: props.direct
            ? props.voyage.PODNAME
            : props.voyage.header.PODNAME,
          port_loading_description: props.direct
            ? props.voyage.POLNAME
            : props.voyage.header.POLNAME,
          vessel_name: props.direct
            ? props.voyage.VESSELNAME
            : props.voyage.header.VESSELNAME,
          notes: form.value.notes,
        })
          .then(({ data }) => {
            loading.value = false;
          })
          .catch(() => {
            loading.value = false;
          });
      }
    };

    onMounted(() => {
      form.value.port_loading = `${
        props.direct ? props.voyage.POLNAME : props.voyage.header.POLNAME
      }(${props.direct ? props.voyage.POL : props.voyage.header.tmh_pol})`;
      form.value.port_discharge = `${
        props.direct ? props.voyage.PODNAME : props.voyage.header.PODNAME
      }(${props.direct ? props.voyage.POD : props.voyage.header.tmh_pod})`;
      form.value.vessel = `${
        props.direct ? props.voyage.VESSELNAME : props.voyage.header.VESSELNAME
      }`;
      form.value.voyage = props.direct
        ? props.voyage.rhreise
        : props.voyage.header.rhreise;
      form.value.currency = bookingQuotation.value.moneda;
      form.value.container_syze_type =
        bookingQuotation.value.tipo_contendeor_code;
      form.value.special_quotation = bookingQuotation.value.code;
      form.value.service = props.direct
        ? props.voyage.service_code
        : props.voyage.header.rhservice;
      form.value.ready_date = props.direct
        ? props.voyage.ETA_POL
        : props.voyage.header.rheta;
      if (Object.keys(user.value).length) {
        form.value.customer_reference = user.value.customer_number;
      }
    });

    return {
      loading,
      form,
      user,
      isauth,
      date_max,
      submitButton,
      updateTariffCode,
      makeBooking,
    };
  },
});
</script>
