
import { defineComponent, ref, onMounted, computed } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";
import moment from "moment";

export default defineComponent({
  name: "Section3",

  setup() {
    const store = useStore();
    const percentage = ref(40);
    const offers = ref<[] | any>([]);
    const tarifas = ref<[] | any>([]);
    const elements = ref<[] | any>([]);
    const user = computed(() => store.getters.currentUser);

    const setActiveButton = (value, item) => {
      //store.commit("setFromSection3", true);
      console.log("item", item);
      store.commit("setContainerType", item.kind);
      //store.commit("setActive", value);
      store.commit("setRatePol", item.pol);
      store.commit("setRatePod", item.pod);
    };

    onMounted(() => {
      ApiService.get("/api/configuration").then(({ data }) => {
        const config = data.find((x) => x.name === "discount_rate");
        percentage.value = config.value;
      });
      ApiService.get("/api/offer").then(({ data }) => {
        offers.value = data;
        offers.value.forEach((offer) => {
          ApiService.query("/api/lima/quotations", {
            params: {
              qt2polcde: offer.pol,
              qt2podcde: offer.pod,
              qt2kopcde: [offer.kind_container],
            },
          }).then(({ data: response }) => {
            tarifas.value = response.tarif;
            tarifas.value.forEach((tarifa) => {
              let total = 0;
              tarifa.forEach((x) => {
                total += parseFloat(x.precio_total);
              });
              const temp = `${tarifa[0]["fecha"].substring(0, 4)}-${tarifa[0][
                "fecha"
              ].substring(4, 6)}-${tarifa[0]["fecha"].substring(6, 8)}`;
              elements.value.push({
                pol: offer.pol,
                pod: offer.pod,
                pol_name: offer.pol_name,
                pod_name: offer.pod_name,
                before: total,
                now: (percentage.value / 100) * total,
                kind: offer.kind_container,
                type: tarifa[0]["tipo_contendeor"],
                moneda: tarifa[0]["moneda"],
                fecha: moment(temp).format("DD-MM-YYYY"),
              });
            });
          });
        });
      });
    });

    return {
      user,
      percentage,
      offers,
      tarifas,
      elements,
      setActiveButton,
    };
  },
});
