
import moment from "moment";
import { computed, ref } from "vue";
import { useStore } from "vuex";
import Booking from "@/components/website/booking/Booking.vue";
import RateResponse from "@/components/website/response/RateResponse.vue";
import RateNotFoundResponse from "@/components/website/response/RateNotFoundResponse.vue";
import ApiService from "@/core/services/ApiService";
import { ElNotification } from "element-plus";

export default {
  name: "DirectRoute",
  components: { RateResponse, RateNotFoundResponse, Booking },
  props: {
    voyage: {
      type: Object,
    },
  },
  setup(props) {
    const loading = ref(false);
    const show_details = ref(false);
    const show_quotation = ref(false);
    const store = useStore();
    const rhhafen_pol = computed(() => store.getters.Pol);
    const rhhafen_pod = computed(() => store.getters.Pod);
    const seeBooking = computed(() => store.getters.SeeBooking);
    const cardRef = ref<[] | any>([]);
    const kind_containers = ref<[] | any>([]);
    const kind_containers_selected = ref<[] | any>([]);
    const quotations = ref<[] | any>([]);
    const nodata = ref<[] | any>([]);

    const formatDate = (date) => {
      return moment(date).format("ll");
    };

    const getKindContainers = () => {
      ApiService.get("/api/kind_container").then(({ data }) => {
        data.forEach(function (value, index) {
          if (value.web_description !== "" && value.web_description !== null) {
            kind_containers.value.push(value);
          }
        });
      });
    };

    const updateKindContainersSelected = (item) => {
      let addFlag = true;
      kind_containers_selected.value.forEach(function (value, index) {
        if (value === item.code && addFlag) {
          kind_containers_selected.value.splice(index, 1);
          addFlag = false;
        }
      });
      if (addFlag) {
        kind_containers_selected.value.push(item.code);
      }
    };

    const getQuotations = (voyage) => {
      if (kind_containers_selected.value.length == 0) {
        ElNotification({
          title: "Error",
          message: "Debe seleccionar como mínimo un tipo de contenedor",
          type: "error",
        });
      } else {
        loading.value = true;
        store.commit("setSeeForm", false);
        store.commit("resetBookingQuotation");
        ApiService.query("/api/lima/quotations", {
          params: {
            qt2polcde: voyage.POL,
            qt2podcde: voyage.POD,
            qt2kopcde: kind_containers_selected.value,
          },
        }).then(({ data }) => {
          quotations.value = data.tarif;
          nodata.value = data.not_found;
          loading.value = false;
        });
      }
    };

    const showQuotation = (value) => {
      const index = cardRef.value.indexOf(value);
      if (index === -1) {
        cardRef.value.push(value);
      }
      if (index !== -1 && show_quotation.value) {
        cardRef.value.splice(index, 1);
      }
      store.commit("setSeeForm", false);
      store.commit("resetBookingQuotation");
      kind_containers_selected.value = [];
      kind_containers.value = [];
      getKindContainers();
      show_quotation.value = true;
      show_details.value = false;
    };

    const showDetails = (value) => {
      const index = cardRef.value.indexOf(value);
      if (index === -1) {
        cardRef.value.push(value);
      }
      if (index !== -1 && show_details.value) {
        cardRef.value.splice(index, 1);
      }
      show_details.value = true;
      show_quotation.value = false;
    };

    const calculateDays = (voyage) => {
      const departure = moment(voyage.ETA_POL).format("YYYY-MM-DD");
      const arrival = moment(voyage.ETA_POD).format("YYYY-MM-DD");
      const date1 = moment(departure);
      const date2 = moment(arrival);
      const duration = moment.duration(date2.diff(date1)).asDays();
      return Math.round(duration) <= 0 ? 1 : Math.round(duration);
    };

    return {
      cardRef,
      quotations,
      nodata,
      show_details,
      show_quotation,
      loading,
      kind_containers,
      kind_containers_selected,
      seeBooking,
      formatDate,
      calculateDays,
      showQuotation,
      showDetails,
      getQuotations,
      updateKindContainersSelected,
      getKindContainers,
    };
  },
};
