
import { computed, defineComponent, ref } from "vue";
import Schedule from "@/components/website/tabs/Schedule.vue";
import Rate from "@/components/website/tabs/Rate.vue";
import { useStore } from "vuex";

export default defineComponent({
  name: "Section2",
  components: {
    Schedule,
    Rate,
  },

  setup() {
    const store = useStore();
    const active = computed(() => store.getters.Active);

    const setActiveButton = (value) => {
      store.commit("setActive", value);
    };

    return {
      active,
      setActiveButton,
    };
  },
});
