
import { computed, onMounted, ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";
import moment from "moment";

export default {
  name: "PortSelect",
  props: {
    port_id: {
      type: String,
      default: "",
    },
    rheta: {
      type: String,
      default: "",
    },
    pol: {
      type: Boolean,
      default: true,
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:port_id"],

  setup(props) {
    const store = useStore();
    const pols_array = computed(() => store.getters.Pols);
    const pods_array = computed(() => store.getters.Pods);
    const element_id = ref(undefined);
    const inputPol = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });
    const inputPod = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });

    const selectPort = (query) => {
      if (query !== "") {
        setTimeout(() => {
          if (props.pol) {
            inputPol.value.loading = true;
            ApiService.query("/api/lima/portLoading", {
              params: { rhhafen: query, rheta: props.rheta },
            }).then(({ data }) => {
              inputPol.value.loading = false;
              inputPol.value.list = data;
              inputPol.value.options = data;
              store.commit("setPols", data);
            });
          } else {
            inputPod.value.loading = true;
            ApiService.query("/api/lima/portDischarge", {
              params: { rhhafen: query, rheta: props.rheta },
            }).then(({ data }) => {
              inputPod.value.loading = false;
              inputPod.value.list = data;
              inputPod.value.options = data;
              store.commit("setPods", data);
            });
          }
        }, 1000);
      } else {
        if (props.pol) {
          inputPol.value.options = [];
        } else {
          inputPod.value.options = [];
        }
      }
    };

    watch(
      () => props.port_id,
      (first) => {
        element_id.value = first;
      }
    );

    onMounted(() => {
      if (props.port_id !== "") {
        if (props.pol && pols_array.value.length) {
          inputPol.value.list = pols_array.value;
          inputPol.value.options = pols_array.value;
        } else if (pods_array.value.length) {
          inputPod.value.list = pods_array.value;
          inputPod.value.options = pods_array.value;
        }
        element_id.value = props.port_id;
      }
    });

    return {
      element_id,
      inputPol,
      inputPod,
      pols_array,
      pods_array,
      selectPort,
    };
  },
};
