
import { onMounted, ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";
import { useStore } from "vuex";

export default {
  name: "TariffCodeSelect",
  props: {
    cccode_id: {
      type: String,
      default: "",
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:cccode_id"],

  setup(props, { emit }) {
    const element_id = ref(undefined);
    const inputElement = ref({
      loading: false,
      options: [] as any,
      list: [] as any,
    });

    const updateCode = ($e) => {
      const item = inputElement.value.list.find((x) => x.cccode === $e);
      emit("updateCode", item);
    };

    const getElements = () => {
      ApiService.get("/api/lima/tariffCode").then(({ data }) => {
        inputElement.value.list = data;
        inputElement.value.options = data;
      });
    };

    const selectElement = (query) => {
      if (query !== "") {
        inputElement.value.loading = true;
        setTimeout(() => {
          inputElement.value.loading = false;
          inputElement.value.options = inputElement.value.list.filter(
            (item: Record<string, any>) => {
              return (
                item.ccbez.toLowerCase().indexOf(query.toLowerCase()) > -1 ||
                item.cccode.toLowerCase().indexOf(query.toLowerCase()) > -1 ||
                item.cchide.toLowerCase().indexOf(query.toLowerCase()) > -1
              );
            }
          );
        }, 200);
      } else {
        inputElement.value.options = inputElement.value.list;
      }
    };

    watch(
      () => props.cccode_id,
      (first) => {
        element_id.value = first;
      }
    );

    onMounted(() => getElements());

    return {
      element_id,
      inputElement,
      updateCode,
      selectElement,
    };
  },
};
